<template>
  <div :class="$root.themeHomeChange === '1' ? 'tableTask_light' : 'tableTask'">
    <div class="header">
      <img src="@/assets/img/companyCenter/jiantou.png" />
      <span class="headerTitle">{{title}}</span>
      <span class="selectBox" >
        <el-select
          v-model="selectVal"
          style="width: 150px;"
          size="mini"
          placeholder="请选择进度情况"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </span>
    </div>
    <div class="content">
      <el-table
        :data="tableData.filter(
          (item) => { return selectVal == '2' ? true : item.status  == selectVal }
        )"
        style="width: 100%"
        height="265">
        <el-table-column
          prop="name"
          label="项目名称"
          width="140"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="stage"
          label="当前阶段"
          width="80"
          align="center">
        </el-table-column>
        <el-table-column
          prop="hierarchy"
          label="层级"
          min-width="80"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="status"
          label="进度情况"
          width="80"
          align="center">
          <template slot-scope="scope">
            <span :style="{color: scope.row.status == '1' ? '#1db583' : '#f9686b', 'font-weight': 'bold'}">
              {{scope.row.status == '1' ? '正常' : '超期'}}
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  export default {
    name: "tableTask",
