          {
            name: '两票管理岗位不相容合规检查',
            stage: '问责',
            hierarchy: '东区配电运维班',
            status: '0'
          },
          {
            name: '五桂山街道迁改项目合规检查',
            stage: '取证',
            hierarchy: '五桂山计量运维班',
            status: '1'
          },
          {
            name: '资金管理岗位不相容合规.',
            stage: '整改',
            hierarchy: '南区配电运维班',
            status: '1'
          },
          {
            name: '石岐区街道迁改项目合规检查',
            stage: '整改',
            hierarchy: '三乡分局',
            status: '1'
          },
          {
            name: '电费电价管理岗位不相容',
            stage: '取证',
            hierarchy: '南区分局',
            status: '1'
          },
          {
            name: '三乡镇迁改项目合规检查',
            stage: '问责',
            hierarchy: '坦洲业主项目部',
            status: '0'
          },
          {
            name: '坦洲镇迁改项目合规检查',
            stage: '问责',
            hierarchy: '东区配电运维班',
            status: '0'
          }
        ]
      }
    },
    mounted() {},
    methods: {}
  }
</script>

