    }
    /deep/ .el-table th.is-leaf {
      /* 去除上边框 */
        border: none;
    }
    
    // 修改鼠标悬浮
    /deep/ .el-table--enable-row-hover .el-table__body tr:hover>td {
      background-color: rgba(67, 137, 249, 0.3);
    }
    
    // 修改下拉框样式
    /deep/ .el-select .el-input__inner {
      background-color: #fff;
