      position: relative;
      .headerTitle {
        font-family: MicrosoftYaHei-Bold;
        font-size: 17px;
        color: #4389F9;
        font-weight: 600;
        margin-left: 5px;
      }
      .selectBox {
        position: absolute;
        right: 10px;
        color: #fff;
        cursor: pointer;
      }
    }
    .content {
      width: 100%;
    }
  }
</style>
