<template>
  <div :class="$root.themeHomeChange === '1' ? 'overView_light' : 'overView'" id="overView">
    <div class="left">
      <div
        class="monitoringData"
        v-loading="monitoringDataLoading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <div class="header">
          <img src="@/assets/img/companyCenter/jiantou.png" />
          <span class="headerTitle">各部门缺陷监测数据</span>
        </div>
        <el-row class="content" :gutter="15">
          <el-col v-for="(item, index) of monitoringData" :key="index" class="item">
            <p class="label" style="font-size: 14px; font-weight: bold;">{{item.name}}</p>
            <p class="totalData">{{item.yearValue}}</p>
            <div>
              <span class="label">当月监测：</span>
              <div>
                <i :class="item.contrast ? 'el-icon-top up' : 'el-icon-bottom down'"> {{item.monthValue}}</i>
              </div>
            </div>
            <div class="label">
              <bda-tooltip :content="'同比：' + (item.yearOnYear || 0) + '% ' + '环比：' + (item.ringRatio || 0) + '%'" placement="bottom">
                <div>
                  同比:{{ item.yearOnYear || 0 }}%
                  环比:{{ item.ringRatio || 0 }}%
                </div>
              </bda-tooltip>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="intelligence">
        <intelligence
          ref="monitor"
          title="智能监测"
          v-loading="monitorLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          @monitorDateChange="getIntelligentData_monitor($event)"
        />
        <intelligence
          ref="grading"
          title="智能定级"
          v-loading="gradingLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          @gradingRadioChange="getIntelligentData_grading($event)"
        />
      </div>
      <complianceTasks ref="scheduleComplianceTasks" title="计划性合规任务缺陷情况汇总"/>
      <realTimeTasks ref="realTimeTasks" title="实时性合规任务缺陷情况汇总"/>
    </div>
    <div class="right">
      <tableCheck ref="table-hgjcjk" title="合规检查近况" />
      <tableTask ref="table-zxxhgrw" style="margin-top: 15px;" title="专项性合规任务" />
      <keyApplications ref="table-zxxhgrw" style="margin-top: 15px;" title="快键应用" />
    </div>
  </div>
</template>

<script>
  import intelligence from '@/views/components/intelligence.vue';
  import tableCheck from '@/views/components/tableCheck.vue';
  import tableTask from '@/views/components/tableTask.vue';
  import complianceTasks from '@/views/components/ehcarts-tasks.vue';
  import keyApplications from '@/views/components/keyApplications.vue';
  import realTimeTasks from '@/views/components/real-timeTasks.vue';
  import Interface from "@/base/mixin/risk-control-center.js";
  export default {
    name: "overView",
    mixins: [Interface],
    components: {
      intelligence,
      tableCheck,
      tableTask,
      complianceTasks,
      keyApplications,
      realTimeTasks
    },
    data(){
      return {
        monitoringData:  [],
        monitoringDataLoading: false,
        monitorLoading: false,
        gradingLoading: false,
      }
    },
    watch: {
      // 监测主题改变
      // '$root.themeHomeChange'(v) {
      //   document.getElementById('overView').style.backgroundColor  = v == '1' ? 'rgba(170, 255, 255, 0.8)' : 'rgba(0, 170, 255, 0.7)';
      // }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        this.getMonitoringData();
        this.getIntelligentData_grading();
        this.getIntelligentData_monitor();
      },
      getMonitoringData() {
        let d = new Date();
        this.monitoringDataLoading = true;
        this.getDeptDefectMonitor({ strDate: (d.getFullYear() + '-' + d.getMonth()) }).then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            this.monitoringData = rs;
          } else {
            this.monitoringData = [];
          }
        }).finally(()=>{
          this.monitoringDataLoading = false;
        })
      },
      // 获取智能监测数据
      getIntelligentData_monitor (newDate) {
        let nowDate = new Date();
        let year = nowDate.getFullYear(); 
        let month = nowDate.getMonth() + 1;
        let day = nowDate.getDate();
        month = (month < 10 ? "0" + month : month);
        
        let yearFirstDay = nowDate.getFullYear() + '-01-01';
        
        newDate = Array.isArray(newDate) && newDate.length !== 0 ? newDate : [yearFirstDay, year + '-' + month + '-' + day];
        
        this.monitorLoading = true;
        
        this.getIntelligentMonitoring({ start: newDate[0], end: newDate[1] }).then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            this.$refs['monitor'].monitorList = rs;
          } else {
            this.$refs['monitor'].monitorList = [
              {
                "type": "实时性",
                "sum": 0,
                "ringRatio": 0,
                "yearOnYear": 0
              },
              {
                "type": "计划性",
                "sum": 0,
                "ringRatio": 0,
                "yearOnYear": 0
              },
              {
                "type": "专项性",
                "sum": 0,
                "ringRatio": 0,
                "yearOnYear": 0
              }
            ];
          }
        }).finally(()=>{
          this.monitorLoading = false;
        })
      },
      // 获取智能定级数据
      getIntelligentData_grading ( type = 3 ) {
        this.gradingLoading = true;
        this.getIntelligentGrading({ type: type }).then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            this.$refs['grading'].gradingList = rs;
          } else {
            this.$refs['grading'].gradingList = [
              {
                  "qxlx": "重大缺陷",
                  "number": 0,
                  "zhanbi": 0,
                  "yearOnYear": 0,
                  "ringRatio": 0
              },
              {
                  "qxlx": "较大缺陷",
                  "number": 0,
                  "zhanbi": 0,
                  "yearOnYear": 0,
                  "ringRatio": 0
              },
              {
                  "qxlx": "一般缺陷",
                  "number": 0,
                  "zhanbi": 0,
                  "yearOnYear": 0,
                  "ringRatio": 0
              },
              {
                  "qxlx": "轻微缺陷",
                  "number": 0,
                  "zhanbi": 0,
                  "yearOnYear": 0,
                  "ringRatio": 0
              }
            ];
          }
        }).finally(()=>{
          this.gradingLoading = false;
        })
      }
    }
  }
</script>

<style scoped lang="scss">
