<template>
  <div :class="$root.themeHomeChange === '1' ? 'tableCheck_light' : 'tableCheck'">
    <div class="header">
      <img src="@/assets/img/companyCenter/jiantou.png" />
      <span class="headerTitle">{{title}}</span>
      <span class="checkMore" @click="goOverManage">更多 >> </span>
    </div>
    <el-table
      id="tableCheck"
      :data="tableData"
      style="width: 100%"
      height="385">
      <el-table-column
        prop="name"
        label="项目名称"
        min-width="100"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="businessDomain"
        label="部门层级"
        min-width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="abnormal"
        label="异常"
        min-width="60">
        <template slot-scope="scope">
          <span style="color: #c4545a; font-weight: bold;">{{scope.row.abnormal}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="type"
        label="类型"
        min-width="70">
      </el-table-column>
      <el-table-column
        prop="updateTime"
        label="更新时间"
        min-width="100">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: "tableCheck",
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    data(){
      return {
        timer: '',
        tableData: [
          {
            name: '南朗镇迁改项目合规检查',
            businessDomain: '办公室',
            abnormal: '5',
            type: '实时性',
            updateTime: '2021-09-29'
          },
          {
            name: '电费电价管理岗位不相容',
            businessDomain: '合规与监审部',
            abnormal: '6',
            type: '计划性',
            updateTime: '2021-09-30'
          },
          {
            name: '三乡镇迁改项目合规检查',
            businessDomain: '系统规划部',
            abnormal: '7',
            type: '专项性',
            updateTime: '2021-10-11'
          },
          {
            name: '坦洲镇迁改项目合规检查',
            businessDomain: '资产管理部',
            abnormal: '8',
            type: '实时性',
            updateTime: '2021-09-17'
          },
          {
            name: '资金管理岗位不相容合规',
            businessDomain: '市场营销部',
            abnormal: '9',
            type: '计划性',
            updateTime: '2021-10-15'
          },
          {
            name: '南朗镇迁改项目合规检查',
            businessDomain: '安全监管部',
            abnormal: '14',
            type: '实时性',
            updateTime: '2021-09-20'
          },
          {
            name: '2021年岗位不相容检查任务',
            businessDomain: '人力资源部',
            abnormal: '9',
            type: '计划性',
            updateTime: '2021-10-15'
          },
          {
            name: '黄圃镇迁改项目合规检查',
            businessDomain: '党建工作部',
            abnormal: '6',
            type: '计划性',
            updateTime: '2021-09-30'
          },
          {
            name: '两票管理岗位不相容合规检查',
            businessDomain: '创新与数字化部',
            abnormal: '7',
            type: '专项性',
            updateTime: '2021-10-11'
          },
          {
            name: '板芙镇迁改项目合规检查',
            businessDomain: '财务部',
            abnormal: '16',
            type: '实时性',
            updateTime: '2021-09-20'
          },
          {
            name: '五桂山街道迁改项目合规检查',
            businessDomain: '财务部',
            abnormal: '12',
            type: '实时性',
            updateTime: '2021-09-25'
          },
        ]
      }
    },
    mounted() {
      this.init();
      let tableDom = document.getElementById('tableCheck');
      tableDom.onmouseenter =  () => {
        this.pause();
      }
      tableDom.onmouseleave =  () => {
        this.init();
      }
    },
    methods: {
      init() {
        clearInterval(this.timer);
        this.tableMove()
        this.timer = setInterval(() =>{
          this.tableMove()
        },1400)
      },
      pause(){
        clearInterval(this.timer);
      },
      tableMove(){
        // 48px 为表格行高
        let t = document.getElementsByClassName('el-table__body-wrapper');
        setTimeout(() =>{
          t[0].style.marginTop = '-10px';
          t[0].style.marginBottom = '10px';
          this.tableData.splice(0, 1);
        }, 800)
        setTimeout(() =>{
          this.tableData.push(this.tableData[0]);
          t[0].style.marginTop = '0px';
          t[0].style.marginBottom = '0px';
        }, 1300)
      },
    goOverManage(){
    this.$router.push({
      path: '/overManage',
      query: {
        index: '3-2',
        actNav: this.$route.query.actNav
        }
    });
  }
    },
    beforeDestroy() {
      this.pause();
    },
    watch: {
      $route(v) {
        if(v.path !== '/riskControlCenter' && v.path !== '/overView' ) {
          this.pause();
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .tableCheck {
    width: calc(100% - 20px);
    height: 400px;
    padding: 10px;
    background: url(../../assets/img/riskControlCenter/hgjcjkBg.png) no-repeat;
    background-size: 100% 100%;
    /deep/ .el-table__body-wrapper {
      transition: all .5s;
    }
    // 覆盖element表格背景
    /deep/ .el-table {
      background: rgba(0, 0, 0, 0);
      th {
        background: rgba(0, 0, 0, 0);
      }
      tr{
        background: rgba(0,0,0,0);
      }
    }
    // 修改字体
    /deep/ .el-table tr td .cell {
      color: #d9d9d9;
      font-size: 10px;
    }
    /deep/ .el-table tr th .cell {
      font-family: MicrosoftYaHei-Bold;
      font-size: 12px;
      color: #00F8FF;
      font-weight: 700;
    }
    
    // 修改框线
    /deep/ .el-table td,.building-top .el-table th.is-leaf {
      border-bottom:  1px solid #0065B3;
    }
    /deep/.el-table::before {
      // border-bottom:  1px solid #0065B3;
      height: 0px
    }
    /deep/ .el-table th.is-leaf {
      /* 去除上边框 */
        border: none;
    }
    
    // 修改鼠标悬浮
    /deep/ .el-table--enable-row-hover .el-table__body tr:hover>td {
      background-color: rgba(0, 101, 179, 0.4);
    }
    .header {
      display: flex;
      align-items: center;
      // padding-left: 10px;
      margin-bottom: 5px;
      position: relative;
      .headerTitle {
        font-family: MicrosoftYaHei-Bold;
        font-size: 17px;
        color: #00F8FF;
        font-weight: 600;
        margin-left: 5px;
      }
      .checkMore {
        position: absolute;
        right: 10px;
        color: #fff;
        cursor: pointer;
      }
    }
  }
  
  .tableCheck_light {
    width: calc(100% - 20px);
    height: 400px;
    padding: 10px;
    // background: url(../../assets/img/riskControlCenter/hgjcjkBg.png) no-repeat;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    border-radius: 6px;
    background-size: 100% 100%;
    position: relative;
    overflow: hidden;
    &::after {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      top: -5px;
      left: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    &::before {
      content: "";   
      display: block;
      width: 50px;   
      height: 5px;
      background: #4085fa;
      position: absolute;
      bottom: -5px;
      right: -15px;
      transform: rotate(-45deg);
      // border-radius: 50px 0;
    }
    
    /deep/ .el-table__body-wrapper {
      transition: all .5s;
    }
    // 覆盖element表格背景
    /deep/ .el-table {
      background: rgba(0, 0, 0, 0);
      th {
        background: rgba(0, 0, 0, 0);
      }
      tr{
        background: rgba(0,0,0,0);
      }
    }
    // 修改字体
    /deep/ .el-table tr td .cell {
      color: #333;
      font-size: 10px;
    }
    /deep/ .el-table tr th .cell {
