      border-bottom:  1px solid #ccc;
    }
    /deep/.el-table::before {
      // border-bottom:  1px solid #0065B3;
      height: 0px
    }
    /deep/ .el-table th.is-leaf {
      /* 去除上边框 */
        border: none;
    }
    
    // 修改鼠标悬浮
    /deep/ .el-table--enable-row-hover .el-table__body tr:hover>td {
      background-color: rgba(67, 137, 249, 0.3);
    }
    
    .header {
      display: flex;
      align-items: center;
      // padding-left: 10px;
      margin-bottom: 5px;
      position: relative;
      .headerTitle {
        font-family: MicrosoftYaHei-Bold;
        font-size: 17px;
        color: #4389F9;
        font-weight: 600;
        margin-left: 5px;
      }
      .checkMore {
        position: absolute;
        right: 10px;
        color: #fff;
        cursor: pointer;
      }
    }
  }
</style>
